olute;\n      margin: 0;\n    }\n\n    @media(max-width: 767px) {\n      width: 70px;\n      height: 70px;\n    }\n\n    @media(max-width: 479px) {\n      width: 65px;\n      height: 65px;\n    }\n  }\n\n  &__button-ico {\n    width: 22px;\n    height: 28px;\n    margin-left: 6px;\n    color: $red-color;\n\n    @media(max-width: 767px) {\n      width: 20px;\n      height: 26px;\n      margin-left: 5px;\n    }\n    @media(max-width: 479px) {\n      width: 16px;\n      height: 22px;\n      margin-left: 4px;\n    }\n  }\n\n  &__wrap {\n    width: 100%;\n    position: relative;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    transition: all .3s ease;\n\n    &_stop {\n      &::before {\n        content: '';\n        position: absolute;\n        top: 0;\n        right: 0;\n        bottom: 0;\n        left: 0;\n        z-index: 1;\n        background: #000;\n        opacity: .7;\n        transition: opacity .2s ease;\n      }\n    }\n\n    &_full {\n      height: 100%;\n      &::after {\n        padding-top: 0;\n      }\n      #{$root} {\n        &__box {\n          object-fit: cover;\n          position: fixed;\n          width: 100%;\n          height: 100%;\n          top: 0;\n          left: 0;\n        }\n        &__progress {\n          height: 8px;\n        }\n      }\n    }\n\n    &::after {\n      content: '';\n      display: block;\n      padding-top: calc(100% / ( 16 / 9));\n      transition: padding .2s ease;\n    }\n  }\n\n  &__box {\n    width: 100%;\n    height: 100%;\n    position: absolute;\n    top: 0;\n    left: 0;\n    cursor: pointer;\n  }\n\n  &__progress {\n    display: flex;\n    position: absolute;\n    width: 100%;\n    height: 5px;\n    bottom: 0;\n    left: 0;\n    background: $dark-color;\n    cursor: pointer;\n    z-index: 3;\n    transition: height .3s ease;\n  }\n\n  &__percent {\n    width: 0;\n    height: 100%;\n    top: 0;\n    left: 0;\n    position: absolute;\n    background: $red-color;\n  }\n\n  &__con